import React, { useState, useEffect } from 'react';
import './TotalScore.css';
import { SERVER_URL } from "./config"; 

const frameworkMapping = {
  4: 'GRI',
  5: 'SASB',
  6: 'TCFD'
};

const TotalScore = ({ companyId, year, frameworkId }) => {
  const [esgScore, setEsgScore] = useState(null);

  useEffect(() => {
    if (companyId && year && frameworkId) {
      const fetchData = async () => {
        try {
          const response = await fetch(`${SERVER_URL}/app/calculateperformance?company=${companyId}&scale=1`, {
            method: 'GET',
            headers: {
              'Content-Type': 'application/json'
            }
          });

          if (!response.ok) {
            throw new Error('Network response was not ok');
          }

         
                    
          //  data  data.GRI  data.SASB
         

          const data = await response.json();
          console.log("Complete data from backend:", data); // 

          const frameworkName = frameworkMapping[frameworkId];
          console.log("Using framework:", frameworkName); // 

          const yearData = data[frameworkName];//  result 
          console.log("Year data for framework:", yearData); // 

          const score = yearData && yearData[year] !== undefined ? yearData[year].toFixed(3) : null;
          console.log("Resolved ESG score for the year:", score); //  ESG 

          setEsgScore(score);

        } catch (error) {
          console.error('Error fetching data:', error);
          setEsgScore(null);
        }
      };

      fetchData();
    } else {
      console.log("Missing parameters: companyId, year, or frameworkId"); // 
      setEsgScore(null);
    }
  }, [companyId, year, frameworkId]);

  return (
    <div className="total-score">
      <h1>ESG Score: {esgScore !== null ? esgScore : 'Unavailable'}</h1>
    </div>
  );
};

export default TotalScore;
